import { IClientSideWebPartManifest } from '@microsoft/sp-module-interfaces';

class MockClientSideWebPartManifests /* implements IClientSideWebPartManifests */  {

    public static getManifest(id: string): IClientSideWebPartManifest<{}> {
        /* tslint:disable:no-any */
        const m: any = (window as any).webpartManifests[id];
        /* tslint:enable:no-any */
        return m ? m : 'Manifest not found';
    }
}

export default MockClientSideWebPartManifests;
